
const request = require('superagent');

const apiEntryPoint ='http://192.168.99.100:8090/simple-app-api/'


const login = (user, callback) => {
    request.post(apiEntryPoint + 'authentication')
            .set('Content-Type', 'application/json')
            .set('Accept', 'text/plain')
            .send(user)
            .end((err, res) =>{
                callback(err, res)
            }) 
}

const logout = (callback)=> {
    callback()
}

const get = (url, callback) => {
    request.get(apiEntryPoint + url)
           .set('Content-Type', 'application/json')
           .set('Accept', 'application/json')
           .set('Authorization', "Bearer " + JSON.parse(sessionStorage.getItem('jwt')))
           .end((err, res) => {
                callback(err, res)
           })
}



const post = (url,body, callback) => {
    request.post(apiEntryPoint + url)
           .set('Content-Type', 'application/json')
           .set('Accept', 'application/json')
           .set('Authorization', "Bearer " + JSON.parse(sessionStorage.getItem('jwt')))
           .send(body)
           .end((err, res) => {
               callback(err,res)
           })
}


const put = (url, body, callback) => {
    request.put(apiEntryPoint + url)
           .set('Content-Type', 'application/json')
           .set('Accept', 'application/json')
           .set('Authorization', "Bearer " + JSON.parse(sessionStorage.getItem('jwt')))
           .send(body)
           .end((err, res) => {
                callback(err,res)
           })
}


const remove = (url, body, callback) => {
    request.delete(apiEntryPoint + url)
           .set('Content-Type', 'application/json')
           .set('Accept', 'application/json')
           .set('Authorization', "Bearer " + JSON.parse(sessionStorage.getItem('jwt')))
           .send(body)
           .end((err, res) => {
                callback(err,res)
           })
}

const auth = {
    isAuthenticated() {
      if (sessionStorage.getItem('jwt'))
        return JSON.parse(sessionStorage.getItem('jwt'))
      else
        return false
    },
    authenticate(jwt, cb) {
      sessionStorage.setItem('jwt', JSON.stringify(jwt))
      cb()
    },
    logout(cb) {
      sessionStorage.removeItem('jwt')
      cb()
    },
    decodeJWT(token){
        var JwtToken = JSON.parse(atob(token.split('.')[1]));
        return JwtToken  
    },
    getSubject(){
        const tokenString = this.decodeJWT(JSON.parse(sessionStorage.getItem('jwt')))
        return tokenString.sub
    }
  }


export{
    get,
    post,
    put,
    remove,
    login,
    logout,
    auth
}